.about-benefits {
    &__info {
        max-width: 35vw;
    }

    &__image {
        grid-column: span 4;
        margin: rem(80px) 0 0;

        @include tablet {
            margin: rem(120px) 0 0;
            grid-column: 2/5;
        }
    }

    img {
        width: 100%;
    }
}