.video-button {
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    margin: 0 auto;
    display: flex;
    justify-content: center;
    align-items: center;

    &__text {
        transform-origin: center;
        animation: rotate 12s linear infinite;
    }
}